.form {
  text-align: center;
  &__label {
    display: block;
    width: 100%;

    &:not(:last-child){
      margin-bottom: 1rem;
    }
  }
  &__btn {
    font-size: 1.8rem;
  }
  &__input {
    display: block;
    width: 100%;
    height: 5rem;
    padding: 1.5rem;
    font-size: 1.6rem;
    line-height: 1.2;

    border-radius: .3rem;
    border: 1px solid #bdbdbd;
    &::placeholder {
        color: rgb(189, 189, 189);
        line-height: 1.2;
    }
  }

  &__area {
    display: block;
    width: 100%;
    height: 10.4rem;
    overflow-y: auto;
    resize: none;
    scrollbar-width: thin;

    padding: 1.5rem;
    font-size: 1.6rem;
    line-height: 1.2;

    border-radius: .3rem;
    border: 1px solid #bdbdbd;
    &::placeholder {
        color: rgb(189, 189, 189);
        line-height: 1.2;
    }
  }
  &__ch:checked+.form__check > .form__box {
    background-color: #dc7d43;
    border-color: transparent;
    background-image: url('./../img/check.png');
    background-position: center;
    background-size: 80%;
    background-repeat: no-repeat;
  }
  &__check {
    margin-top: 1.2rem;
    text-align: left;
    display: flex;
    align-items: flex-start;
    gap: 1.5rem;

    span {
      font-size: 1.2rem;
      color: rgb(197, 197, 197);
      line-height: 1.333;
    }
  }
  &__box {
    cursor: pointer;
    display: block;
    flex-shrink: 0;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: .2rem;
    border: 1px solid #bdbdbd;
  }
}
